//no module name required since requirejs uses the file name for the module name.
define('data/taskData',[], function () {
    'use strict';

    var STORE_NAME = "tasks";

    function saveTaskData (tasks) {
        localStorage.setItem(STORE_NAME, JSON.stringify(tasks));
    }

    function loadTaskData () {
        var storedTasks = localStorage.getItem(STORE_NAME);
        if(storedTasks) {
            return JSON.parse(storedTasks);
        }
        return [];
    }

    function clearTaskData () {
        localStorage.removeItem(STORE_NAME);
    }
    
    return {
        save: saveTaskData,
        load: loadTaskData,
        clear: clearTaskData
    };
});

